.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

main {
  display: block;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#left-view-home {
  display: block;
  float: left;
}

.left-view {
  width: 40%;
  height: calc(100% - 40px);
  overflow: auto;
  margin: 0px;
  padding-bottom: 80px;
}



@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

/* 414px is iPhone+ */
@media (max-width: 414px) {
  main {
    display: block;
  }

  .left-view {
    width: 100%;
    height: 50%;
    overflow: auto;
  }
}
